.btn {
  font-size: 1rem;
  font-weight: 700;
  padding: 1rem 2.5rem;
  border-radius: 1rem;
  line-height: 100%;
  cursor: pointer;
  transition: all 0.2s ease-in;
}
.btn__primary {
  border: 2px solid #E4C1AE;
  background: #E4C1AE;
  color: #ffffff;
}
.btn__primary:hover, .btn__primary:active {
  background: #ffffff;
  color: #E4C1AE;
}
.btn__primary:active {
  transform: translateY(4px);
}
.btn__secondary {
  background: #7bc2d6;
  border: 2px solid #7bc2d6;
  color: #ffffff;
}
.btn__secondary:hover, .btn__secondary:active {
  background: #ffffff;
  color: #7bc2d6;
}
.btn__secondary:active {
  transform: translateY(4px);
}
.btn-pill {
  border-radius: 2rem;
}
.btn-small {
  padding: 0.8rem 2.4rem;
}

.img-fluid {
  max-width: 100%;
  height: auto;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.modal {
  background: #ffffff;
  color: #121212;
  max-width: 920px;
  width: calc(100% - 1rem);
  padding: 1.5rem;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 999;
  display: none;
  animation-name: fadeIn;
  animation-duration: 0.4s;
  animation-timing-function: ease-in-out;
}
@media (min-width: 768px) {
  .modal {
    padding: 3.5rem 6rem;
  }
}
.modal__close {
  position: absolute;
  top: 1.25rem;
  right: 1.25rem;
  background: transparent;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .modal__close {
    top: 2.5rem;
    right: 2.5rem;
  }
}
.modal__close svg {
  display: block;
  width: 1.5rem;
  height: 1.5rem;
}
.modal__description {
  margin-top: 1.5rem;
  font-weight: 300;
}
.modal__description strong {
  font-weight: 600;
}
.modal .btn {
  margin-top: 1rem;
  font-weight: 300;
  box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.1);
}
.modal .btn svg {
  vertical-align: bottom;
  margin-right: 5px;
}

.modal-active {
  overflow: hidden;
}

.modal-overlay {
  position: fixed;
  background: rgba(18, 18, 18, 0.5);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  animation-name: fadeIn;
  animation-duration: 0.4s;
  animation-timing-function: ease-in-out;
}

h2 {
  line-height: 120%;
  font-size: 1.875rem;
}
@media (min-width: 768px) {
  h2 {
    font-size: 2.625rem;
  }
}

p {
  line-height: 150%;
  font-size: 1rem;
}

.app-section {
  margin-top: 3rem;
}
@media (min-width: 992px) {
  .app-section {
    margin-top: 6rem;
  }
}
.app-article {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 90%;
  margin: 0 auto;
}
@media (min-width: 992px) {
  .app-article {
    flex-direction: row;
    width: 70%;
  }
}
.app-article__picture {
  width: 83.3333333333%;
}
.app-article__picture img {
  display: block;
}
@media (min-width: 576px) {
  .app-article__picture {
    width: 66.6666666667%;
  }
}
@media (min-width: 992px) {
  .app-article__picture {
    width: 58.3333333333%;
  }
}
.app-article__content {
  width: 83.3333333333%;
  margin-top: 1.5rem;
}
@media (min-width: 576px) {
  .app-article__content {
    width: 66.6666666667%;
  }
}
@media (min-width: 992px) {
  .app-article__content {
    width: 41.6666666667%;
    margin-left: 3rem;
    margin-top: 0;
  }
}
@media (min-width: 1200px) {
  .app-article__content {
    margin-left: 6rem;
  }
}
.app-article__description {
  margin-top: 1.5rem;
  margin-bottom: 2rem;
  font-weight: 300;
  text-align: justify;
}
@media (min-width: 992px) {
  .app-article__description {
    text-align: unset;
  }
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

::selection {
  color: #121212;
  background: #E4C1AE;
}

body {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  background: #ffffff;
}

.container {
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}